*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montserrat', sans-serif;
  background-color: #223343;
}

header{
  padding: 50px 15px 0px;
}
 
header h1{
  color: #FFF;
  font-size: 48px;
  font-weight: 500px;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0px auto;
}

section{
  padding: 15px;
}

.searchbox{
  display: block;
  width: 100%;
  padding: 15px;

  border:none;
  outline: none;
  background-color: none;

  background-color: #EEE;
  border-radius: 8px;

  color: #53565A;
  font-size: 20px;
  font-weight: 300px;

  transition: 0.4s ease-out;
}

.searchbox:focus{
  box-shadow: 0px 0px 8px 3px #4484C4
}

.results{
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px
}

.results .result{
  width: 33.333%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .result img{
  display: block;
  width: 100%;
}

.results .result h3{
  color: #FFF;
  font-size: 20px;
  font-weight: 600px;
  padding: 10px;
  background: #445565;
  flex: 1 1 100%;
  transition: 0.4s easeout;
}

.results .result:hover{
  background: #FFF;
  color: #223343;
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #445565;
}

.popup .content{
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;
  color: #FFF;
}

